import React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import { TbUsers, TbTools, TbWand, TbMoodConfuzed, TbStairsUp, TbPennant } from 'react-icons/tb'
import { MdFormatQuote } from 'react-icons/md'
import MockupGallery from '../components/MockupGallery'
import CaseStudySectionWrapper from '../components/CaseStudySectionWrapper'
import slugify from 'slugify'

import Seo from '../components/seo'
import { Helmet } from 'react-helmet'

const CaseStudiesTemplate = ({data:{contentfulCaseStudies}}) => {
    const {
        title,
        projectOverview,
        projectDuration,
        problem,
        goal,
        myRole,
        responsibilities,
        projectOverviewImage,
        heroImage,
        userResearchSummary,
        painPoints,
        personas,
        // userJourneyMap
        paperWireframes,
        digitalWireframes,
        lowFidelityPrototype,
        usabilityStudyFindings,
        usabilityStudyMockups,
        mockups,
        highFidelityPrototype,
        accessibilityConsiderations,
        takeaways,
        caseStudyDate
    } = contentfulCaseStudies
    const slug = slugify(title, { lower: true })

    return (
        <div className='pb-16'>
            <Helmet bodyAttributes={{ class: `${slug}` }} />
            <section className='bg-black bg'>
                <GatsbyImage className='h-36 md:h-64' image={getImage(heroImage)} alt={heroImage.description} objectFit='cover' />
            </section>
            {/* Project Overview */}
            <CaseStudySectionWrapper>
                <div className='grid grid-cols-1 gap-8 lg:gap-16 mt-4 md:grid-cols-1 lg:mt-8 lg:grid-cols-2'>
                    <article className='lg:max-w-2xl my-4 col-span-1'>
                        <h2 className='text-4xl font-extrabold'>Project Overview</h2>
                            <p className='mt-8'>{projectOverview.projectOverview}</p>
                        <h3 className='text-2xl font-bold mt-12'>Project Duration</h3>
                            <p className='mt-4 font-light'>{projectDuration}</p>
                    </article>
                    <figure className='col-span-1 flex items-center'>
                        <GatsbyImage className='' image={getImage(projectOverviewImage)} alt={projectOverviewImage.description} objectFit='contain' />
                    </figure>
                </div>
            </CaseStudySectionWrapper>

            <CaseStudySectionWrapper>
                <div className='grid grid-cols-1 gap-4 md:gap-8 lg:gap-12 md:grid-cols-2 md:mb-16'>
                    <article className='mb-4 md:mb-0'>
                        <h3 className='text-2xl font-bold pt-4 lg:mt-8'>The Problem</h3>
                        <p className='lg:max-w-md mt-4'>{problem}</p>
                    </article>
                    <article className='mb-4 md:mb-0'>
                        <h3 className='text-2xl font-bold pt-4 lg:mt-8'>The Goal</h3>
                        <p className='lg:max-w-md mt-4'>{goal}</p>
                    </article>
                    <article className='mb-4 md:mb-0'>
                        <h3 className='text-2xl font-bold pt-4 lg:mt-8'>My Role</h3>
                        <p className='lg:max-w-md mt-4'>{myRole}</p>
                    </article>
                    <article className='mb-4 md:mb-0'>
                        <h3 className='text-2xl font-bold pt-4 lg:mt-8'>Responsibilities</h3>
                        <p className='lg:max-w-md mt-4'>{responsibilities}</p>
                    </article>
                </div>
            </CaseStudySectionWrapper>

            {/* UNDERSTANDING THE USER */}

            <CaseStudySectionWrapper background='bg-gray-100'>
                <section className='py-16 flex justify-center'>
                    <article className='max-w-6xl text-center flex flex-col'>
                        <TbUsers className='text-6xl mb-8 text-gray-400 place-self-center' />
                        <h2 className='text-4xl font-extrabold'>UNDERSTANDING THE USER</h2>
                        <p className='pt-8'>{userResearchSummary.userResearchSummary}</p>
                    </article>
                </section>
                <section className='grid grid-cols-1 md:grid-cols-2 gap-8 md:gap-12'>
                    {painPoints.map((item) => {
                        return (
                            <div key={item.key} className='max-w-xl mb-8'>
                                <h3 className='text-2xl font-bold'>{item.key}.</h3>
                                <p className='mt-4 lg:max-w-md'>{item.content}</p>
                            </div>
                        )
                    })}
                    {/* <GatsbyImage image={getImage(userJourneyMap)} alt={userJourneyMap.description} objectFit='contain' /> */}
                </section>
            </CaseStudySectionWrapper>
            <CaseStudySectionWrapper background='bg-gray-200'>
                <section className='py-2 md:py-8 grid grid-cols-1 lg:grid-cols-2 gap-8 lg:gap-12'>
                    {personas.map((item) => {
                        return (
                            <GatsbyImage image={getImage(item)} key={item.id} alt={item.description} objectFit='cover' className='drop-shadow-xl' />
                        )
                    })}
                </section>
            </CaseStudySectionWrapper>

            {/* STARTING THE DESIGN */}

            <CaseStudySectionWrapper>
                <section className='py-4 md:py-8 flex flex-col justify-center text-center'>
                    <TbTools className='text-6xl mb-8 text-gray-400 place-self-center' />
                    <h2 className='text-4xl font-extrabold'>STARTING THE DESIGN</h2>
                </section>
                <section className='grid grid-cols-1 gap-4 md:gap-8 lg:gap-12 lg:grid-cols-3 py-8'>
                    <article className='my-4'>
                        <h3 className='text-2xl font-bold'>Paper Wireframes</h3>
                        <p className='w-full lg:max-w-xl mt-8'>{paperWireframes.description}</p>
                    </article>
                    <GatsbyImage className='mt-4 lg:mt-16 lg:col-span-2' image={getImage(paperWireframes)} alt={paperWireframes.description} objectFit='contain' />
                </section>
                <section className='my-4 mt-8'>
                    <h3 className='text-2xl font-bold'>Digital Wireframes</h3>
                    <div className='mt-8 grid grid-cols-1 gap-4 lg:grid-cols-2'>
                        {
                            digitalWireframes.map((item) => {
                                return (
                                    <article key={item.id} className='my-8'>
                                        <GatsbyImage image={getImage(item)} alt={item.description} objectFit='contain' />
                                        <div className='flex justify-center mt-4'>
                                            <span className=' text-emerald-600'><TbWand /></span>
                                            <p className='ml-2 font-light text-sm'>{item.description}</p>
                                        </div>
                                    </article>
                                )
                            })
                        }
                    </div>
                </section>
            </CaseStudySectionWrapper>
            <CaseStudySectionWrapper background='bg-gray-900'>
                <section className='grid grid-cols-1 lg:grid-cols-3 lg:py-8'>
                    <article>
                        <h3 className='text-white text-2xl font-bold'>Low-fidelity Prototype</h3>
                        <p className='text-gray-300 max-w-xl mt-8'>{lowFidelityPrototype.description}</p>
                    </article>
                    <GatsbyImage className='mt-8 lg:mt-16 lg:mx-16 lg:col-span-2' image={getImage(lowFidelityPrototype)} alt={lowFidelityPrototype.description} objectFit='contain' />
                </section>
            </CaseStudySectionWrapper>
            <CaseStudySectionWrapper>
                <h3 className='text-2xl font-bold'>Usability Study Findings</h3>
                <p className='lg:max-w-5xl mt-8'>{usabilityStudyFindings.description}</p>
                <div className='mt-8 lg:mb-12 grid grid-cols-1 lg:grid-cols-2 gap-4 lg:gap-12'>
                    {usabilityStudyFindings.findings.map((item, index) => {
                        return (
                            <article className='p-6 lg:p-8 bg-gray-200 relative' key={'findings-' + index}>
                                <h4 className='text-2xl'>{item.title}</h4>
                                <ul className='list-disc'>
                                    {item.entries.map((subItem, index) => {
                                       return <li className='m-4' key={'findings-entries-' + index}>{subItem}</li>
                                    })}
                                </ul>
                                <TbMoodConfuzed className='absolute bottom-4 right-4 text-8xl text-gray-700 opacity-10' />
                            </article>
                        )
                    })}
                </div>
            </CaseStudySectionWrapper>

            {/* REFINING THE DESIGN */}
            <CaseStudySectionWrapper background='bg-gray-200'>
                <div className='py-4 md:py-8 flex flex-col justify-center text-center'>
                    <TbWand className='text-6xl mb-8 text-gray-400 m-auto' />
                    <h2 className='text-4xl font-extrabold mb-12'>REFINING THE DESIGN</h2>

                    {usabilityStudyMockups.map((item) => {
                        return (
                            <article key={item.id}>
                                <p className='max-w-3xl m-auto my-8'>{item.description}</p>
                                <GatsbyImage image={getImage(item)} alt={item.description} objectFit='contain' />
                            </article>
                        )
                    })}
                </div>
            </CaseStudySectionWrapper>
            <section className='bg-gray-300 pt-16 flex flex-col justify-center text-center'>
                <h3 className='text-3xl font-bold'>Mockups</h3>
                <MockupGallery galleryID='mockup-gallery' images={mockups} />
            </section>
            <CaseStudySectionWrapper background='bg-gray-900'>
            <section className='py-4 flex flex-col justify-center text-center'>
                <h3 className=' text-white text-3xl font-bold'>High Fidelity Prototype</h3>
                <p className='text-gray-300 mt-8 lg:max-w-3xl m-auto'>{highFidelityPrototype.description}</p>
                <GatsbyImage image={getImage(highFidelityPrototype)} alt={highFidelityPrototype.title} objectFit='contain' className='my-8' />
            </section>
            </CaseStudySectionWrapper>
            <CaseStudySectionWrapper background='bg-gray-200'>
                <h3 className='text-2xl font-bold mb-8'>Accessibility Considerations</h3>
                <div className='grid grid-cols-1 gap-4 lg:grid-cols-2 lg:gap-24'>
                    {accessibilityConsiderations.accessibility.map((item, index) => {
                        return (
                            <article className='bg-gray-200 relative my-4' key={'accessibility-' + index}>
                                <p>{item}</p>
                            </article>
                        )
                    })}
                </div>
            </CaseStudySectionWrapper>

            {/* GOING FORWARD */}

            <CaseStudySectionWrapper className='flex flex-col justify-center'>
                <TbStairsUp className='text-6xl my-8 text-gray-400 self-center m-auto' />
                <h2 className='text-4xl font-extrabold text-center'>GOING FORWARD</h2>
                <div className='mt-12 grid grid-cols-1 lg:grid-cols-2 gap-8'>
                    <article className='p-2'>
                        <h3 className='text-3xl font-bold'>Impact</h3>
                        <p className='mt-8 lg:max-w-md'>{takeaways.Impact}</p>
                    </article>
                    <article className='p-2'>
                        <h3 className='text-3xl font-bold'>What I learned</h3>
                        <p className='mt-8 lg:max-w-md'>{takeaways.What_I_learned}</p>
                    </article>
                </div>
                <div className='bg-gray-100 self-center px-8 lg:px-24 py-12 my-12 relative'>
                    <MdFormatQuote className='text-gray-300 absolute top-1 left-2 text-6xl' />
                    <p className=' max-w-3xl font-extralight text-2xl italic'>{takeaways.quote}</p>
                    <p className='text-right mt-8 font-light'>-- One quote from peer feedback</p>
                </div>
                <span className=' text-gray-700 text-4xl'><TbPennant className='m-auto' /></span>
                <span className='font-light italic flex justify-center mt-4'>Case Study Date: {caseStudyDate}</span>
            </CaseStudySectionWrapper>
        </div>
    )
}

export const query = graphql`
  query ($title: String) {
    contentfulCaseStudies(title: {eq: $title}) {
      thumbnail {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
      }
      heroImage {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
      }
      title
      subtitle
      projectOverview {
        projectOverview
      }
      projectDuration
      projectOverviewImage {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
      }
      problem
      goal
      myRole
      responsibilities
      userResearchSummary {
        userResearchSummary
      }
      painPoints {
        key
        content
      }
      personas {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        title
        description
        id
      }
      userJourneyMap {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
      }
      paperWireframes {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
      }
      digitalWireframes {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
        id
      }
      lowFidelityPrototype {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
      }
      lowFidelityPrototypeUrl
      usabilityStudyFindings {
        findings {
          title
          entries
        }
        description
        title
      }
      usabilityStudyMockups {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        description
        title
        id
      }
      mockups {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        title
        description
        id
      }
      highFidelityPrototype {
        gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
        title
        description
      }
      highFidelityPrototypeUrl
      accessibilityConsiderations {
        accessibility
      }
      takeaways {
        Impact
        What_I_learned
        quote
      }
      nextSteps
      caseStudyDate
      id
    }
  }
`

export const Head = ({data:{contentfulCaseStudies}}) => <Seo title={contentfulCaseStudies.title} />

export default CaseStudiesTemplate
