import React, { useEffect } from 'react'
import PhotoSwipeLightbox from 'photoswipe/lightbox';
import { GatsbyImage, getImage, getSrc } from 'gatsby-plugin-image';
import { Navigation, Pagination, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'photoswipe/style.css';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

const MockupGallery = ({galleryID, images}) => {
    useEffect(() => {
        let lightbox = new PhotoSwipeLightbox({
          gallery: '#' + galleryID,
          children: 'a',
          pswpModule: () => import('photoswipe'),
        });
        lightbox.init();

        return () => {
          lightbox.destroy();
          lightbox = null;
        };
      }, []);

  return (
    <div className="pswp-gallery pb-12" id={galleryID}>
        <Swiper
            modules={[Navigation, Pagination, A11y]}
            // spaceBetween={}
            // slidesPerView={4}
            navigation
            pagination={{ clickable: true }}
            breakpoints={{
                480: {
                    slidesPerView: 1,
                    spaceBetween: 20,
                  },
                640: {
                  slidesPerView: 2,
                  spaceBetween: 20,
                },
                768: {
                  slidesPerView: 3,
                  spaceBetween: 40,
                },
                1024: {
                  slidesPerView: 4,
                  spaceBetween: 50,
                },
            }}
        >
        {images.map((image, index) => {
            return (
                <SwiperSlide className='px-8 py-16 xs:px-4 xs:py-8 sm:px-6 sm:py-12 md:px-6 md:py-12 lg:px-8 lg:py-16 drop-shadow-lg' key={galleryID + '-' + index}>
                <a
                    href={getSrc(image)}
                    data-pswp-width={getImage(image).width}
                    data-pswp-height={getImage(image).height}
                    target="_blank"
                    rel="noreferrer"
                    >
                <GatsbyImage key={image.id} image={getImage(image)} alt={image.description} />
                </a>
                </SwiperSlide>
            )
      })}

        </Swiper>
    </div>
  )
}

export default MockupGallery
